@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.pre-loader {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 100;

  background: black;
  opacity: 0;
  animation: 4s cubic-bezier(0.001, 0.11, 0.64, 0.64) move_eye;
}

.first-part,
.second-part {
  font-size: 8em;
  color: white;
  font-family: monospace;
}

@keyframes move_eye { 
  0% { 
    opacity: 1; 
    transform: scale(1);
  } 

  30% { 
    opacity: 1;
    transform: scale(1);
  } 

  95% {
    opacity: 1;
    transform: scale(2);
  }
  
  100% { 
    opacity: 0;
    transform: scale(4.5);
  }  
}


.A-Team {
  height: 100vh;
}
.content {
  width: 100%;
  height: 100%;
}

.name {
  position: absolute;
  top: 3%;
  left:3%;
  font-size: 45px;
  font-family: monospace;
  color: white;
  background: black;
  padding: 0 10px;
}

h2 {
  margin: 20px 30px;
}

.middle-section {
  width: 100%;
  text-align: center;
  font-family: monospace;
  margin-top: 80px;
  font-size: 27px;
}

.hero-section {
  overflow: hidden;
  height: 40%;
}

.hero-logo {
  width: 100%;
}



/* Google Fonts */

/* Global */
.line-1{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  border-right: 2px solid black;
  font-size: 20px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  transform: translateY(-50%);    
}

/* Animation */
.anim-typewriter{
  animation: typewriter 4s steps(45) 1s 1 normal both,
  blinkTextCursor 500ms steps(45) infinite normal;
  animation-delay: 6s;
}
@keyframes typewriter{
  from{width: 0;}
  to{width: 24.6em;}
}
@keyframes blinkTextCursor{
  from{border-right-color: black;}
  to{border-right-color: transparent;}
}
